import React from 'react';
import {
  DropdownButton, Dropdown, ButtonGroup, Button,
} from 'react-bootstrap';

interface props {
  onFormatClick: Function;
}

const formats = [
  ['normal', ' Normal abc'],
  ['sansBold', 'Bold (Sans) ùóÆùóØùó∞'],
  ['sansItalic', 'Italic (Sans) ùò¢ùò£ùò§'],
  ['sansBoldItalic', 'Bold Italic (Sans) ùôñùôóùôò'],
  ['bold', 'Bold (Serif) ùêöùêõùêú'],
  ['italic', 'Italic (Serif) ùëéùëèùëê'],
  ['boldItalic', 'Bold Italic (Serif) ùíÇùíÉùíÑ'],
  ['script', 'Script ùí∂ùí∑ùí∏'],
  ['scriptBold', 'Script Bold ùì™ùì´ùì¨'],
  ['fraktur', 'Fraktur ùîûùîüùî†'],
  ['frakturBold', 'Frak Bold ùñÜùñáùñà'],
  ['monospace', 'Monospace ùöäùöãùöå'],
  ['doublestruck', 'Double ùïíùïìùïî'],
  ['circled', 'Circled ‚ìê‚ìë‚ìí'],
  ['inverseCircled', 'Circle 2 üÖêüÖëüÖí (CAPS)'],
  ['squared', 'Squared üÑ∞üÑ±üÑ≤ (CAPS)'],
];

const FormatButtons = ({ onFormatClick }: props) => (
  <>
    <span className="mr-1 text-primary font-weight-light d-none d-lg-inline-block">Format: </span>
    <Button
      className="mr-1"
      variant="outline-primary"
      onClick={() => {
        onFormatClick('normal');
      }}
      onMouseDown={(e) => {
        e.stopPropagation();
        e.preventDefault();
      }}
      as="div"
    >
      Normal
    </Button>
    <Button
      className="mr-1"
      variant="outline-primary"
      onClick={() => {
        onFormatClick('sansBold');
      }}
      onMouseDown={(e) => {
        e.stopPropagation();
        e.preventDefault();
      }}
      as="div"
    >
      ùóïùóºùóπùó±
    </Button>
    <Button
      className="mr-1"
      variant="outline-primary"
      onClick={() => {
        onFormatClick('sansItalic');
      }}
      onMouseDown={(e) => {
        e.stopPropagation();
        e.preventDefault();
      }}
      as="div"
    >
      ùòêùòµùò¢ùò≠ùò™ùò§
    </Button>
    <DropdownButton
      onSelect={(format) => {
        onFormatClick(format);
      }}
      as={ButtonGroup}
      key="formats"
      id="dropdown-variants-formats"
      variant="outline-primary"
      title="More..."
      onMouseDown={(e) => {
        e.stopPropagation();
        e.preventDefault();
      }}
    >
      {formats.map(([format, display]) => (
        <Dropdown.Item key={`fdd_${format}`} eventKey={format}>{display}</Dropdown.Item>
      ))}
    </DropdownButton>
  </>
);

export default FormatButtons;
